@import "../mixins";
.bonus{
  padding: 2.5em 0 4.375em;
  &__inner{
    padding: 5.625em 2em 6.75em;
    border: 1px solid rgba($black, 0.2);
    text-align: center;
  }
  &__title{
    font-size: 2.5em;
    line-height: 125%;
    text-transform: uppercase;
  }
  &__desc{
    margin: 3.125em auto 0;
    max-width: 53.75em;
    color: rgba($black,.5);
    b{
      color: $black;
      display: block;
    }
  }
  &__quote{
    text-align: center;
    padding: 1em 0;
    margin-top: 3.95em;
    position: relative;
    &:before,
    &:after{
      content: '';
      position: absolute;
      height: 1px;
      width: 7.67em;
      background-color: rgba($black,.5);
      left: 50%;
      transform: translateX(-50%);
    }
    &:before{
      top: 0;
    }
    &:after{
      bottom: 0;
    }
  }
  &__nav{
    margin-top: 5em;
    display: flex;
    justify-content: center;
  }
  &__btn{
    text-transform: uppercase;
  }
}

.how-work{
  padding: 4.375em 0 5.625em;
  .display1{
    text-align: center;
  }
  &__grid{
    margin-top: 6em;
    display: grid;
    grid-template-columns: repeat(4,1fr);
    position: relative;
    &:before{
      content: '';
      position: absolute;
      top: 1.875em;
      left: 0;
      width: 100%;
      height: 1px;
      background: $black;
    }
  }
}

.how-work-card{
  &._active{
    .how-work-card__num{
      background: $black;
      border-color: $black;
      color: #fff;
    }
  }
  &:last-child{
    .how-work-card__top{
      background-color: #fff;
    }
  }
  &__top{
    position: relative;
    z-index: 2;
  }
  &__num{
    background-color: #fff;
    width: 3.75em;
    height: 3.75em;
    @include center;
    border: 1px solid rgba($black,.2);
    font-weight: 500;
    border-radius: 50%;
    span{
      letter-spacing: 0.045em;
      font-size: 1.125em;
    }
  }
  &__desc{
    margin-top: 2.75em;
    max-width: 12.5em;
    p{
      font-size: 1em;
      line-height: 125%;
    }
  }
}

.easy-reg{
  padding: 5.625em 0 11.25em;
  .display1{
    text-align: center;
  }
  &__grid{
    margin-top: 5em;
  }
}

.easy-reg-card{
  padding: 3.5em 2.1em;
  background-color: $grayBg;
  &__title{
    font-size: 1.5em;
    line-height: 125%;
  }
  &__nav{
    margin-top: 1.5em;
    display: flex;
  }
  &__stores{
    margin-top: 3.125em;
    display: flex;
  }
  &__store{
    width: 6.875em;
    display: block;
    margin-right: 1.25em;
    img{
      width: 100%;
    }
  }
}

.categories{
  padding: 5em 0 5.5em;
  background-color: $grayBg;
  .display1{
    text-align: center;
  }
  &__grid{
    display: grid;
    grid-template-columns: 1fr 1fr;
    gap: 2.5em;
    margin-top: 5em;
  }
  &__text{
    margin-top: 3.625em;
    @include grid100gap(3.625em);
    max-width: 54.125em;
  }
  &__nav{
    display: flex;
    margin-top: 2.625em;
  }
}

.category-card{
  background-color: #fff;
  padding: 6.25em 3.75em 3em;
  &__title{
    max-width: 13em;
  }
  &__percent{
    margin-bottom: 2.5em;
    padding: 2.5em 0;
    border-bottom: 1px solid #000000;
    span{
      font-size: 3em;
      line-height: 125%;
    }
  }
}

.text-block{
  li,
  p{
    font-size: 1.125em;
    line-height: 123%;
  }
  ul{
    padding-left: .625em;
    li{
      display: flex;
      &:before{
        content: '';
        display: block;
        width: .33em;
        height: .33em;
        border-radius: 50%;
        background-color: $black;
        margin-right: .66em;
        flex-shrink: 0;
        transform: translateY(.45em);
      }
    }
  }
  &._small{
    p{
      font-size: .875em;
    }
  }
}


@media screen and (max-width: 992px){
  .bonus{
    padding: 2.875em 0 4em;
    &__inner{
      padding: 0;
      border: none;
    }
    &__desc{
      margin-top: 2.5em;
      max-width: 16.75em;
    }
    &__quote{
      font-size: 1.25em;
      margin-top: 3em;
      &:before,
      &:after{
        width: 9.2em;
      }
    }
    &__nav{
      margin-top: 2.75em;
    }
  }

  .how-work{
    padding: 4em 0 3.125em;
    &__grid{
      grid-template-columns: 100%;
      gap: 5.75em;
      margin-top: 2.75em;
      &:before{
        width: 1px;
        height: 100%;
        top: 0;
        left: 1.875em;
      }
    }
  }

  .how-work-card{
    display: grid;
    grid-template-columns: 3.75em auto;
    align-items: center;
    gap: 3em;
    &__desc{
      margin: 0;
      max-width: unset;
      br{
        display: none;
      }
    }
  }

  .easy-reg{
    padding: 3.125em 0 6.25em;
    &__grid{
      margin-top: 5.625em;
    }
  }

  .categories{
    padding: 3.25em 0 4.625em;
    &__grid{
      grid-template-columns: 100%;
      margin-top: 3.375em;
      gap: 1.875em;
    }
    &__text{
      gap: 3em;
    }
    &__nav{
      justify-content: center;
    }
  }

  .category-card{
    padding: 4.375em 2.875em;
    &__percent{
      padding: 1.125em 0;
      margin-bottom: 1.125em;
      span{
        font-size: 2em;
      }
    }
  }

  .text-block{
    p,li{
      font-size: 1em;
    }
    &._small{
      p{
        font-size: 1em;
      }
    }
  }
}


@media screen and (max-width: 576px){
  .easy-reg{
    &__grid{
      gap: 1.25em;
    }
  }
}
