@import "../mixins";
.about-page{
  padding: 5em 0 3.75em;
  &__top{
    display: flex;
    justify-content: space-between;
  }
  &__info{
    max-width: 43.125em;
    @include grid100gap(3.125em);
  }
  &__nav{
    padding: 1.875em 0;
  }
  &__links{
    display: flex;
    align-items: center;
  }
  &__subtitle{
    font-weight: 500;
    font-size: .75em;
    line-height: 125%;
    letter-spacing: 0.155em;
    color: rgba(0, 0, 0, 0.5);
    margin-left: 3.5em;
  }
  &__socials{
    display: flex;
    align-items: center;
    &-item{
      width: 1.25em;
      @include center;
      margin-left: .625em;
      img{
        max-width: 100%;
        transition: all .3s;
      }
      &:hover{
        img{
          transform: translateY(-.5em);
        }
      }
    }
  }
  &__action{
    display: flex;
    align-items: center;
    flex-wrap: wrap;
    margin-top: 2em;
    &-btn{
      margin: .5em 3.75em 0 0;
      padding: .625em 0;
      &:before{
        height: .125em;
      }
      &:last-child{
        margin-right: 0;
      }
    }
  }
  &__bottom{
    margin-top: 7.75em;
    display: grid;
    grid-template-columns: 40em auto;
    align-items: center;
    gap: 4em 7.5em;
  }
  &__text{
    @include grid100gap(1.875em);
    p{
      font-size: 1.125em;
      line-height: 122%;
    }
  }
}

.about-contacts{
  &__group{
    display: flex;
    align-items: center;
    justify-content: space-between;
    &:not(:last-child){
      margin-bottom: 2.25em;
    }
  }
  &__address{
    font-size: .875em;
    line-height: 197%;
    text-transform: uppercase;
    font-weight: 500;
  }
}

.interior{
  padding: 3.75em 0;
  &__slider{
    margin-top: 5.625em;
  }
  .swiper{
    &-slide{
      padding-right: 1.6875em;
      width: 36.4375em;
      max-width: 100%;
    }
  }
  &__grid{
    margin-top: 3.125em;
  }
}

.interior-card{
  &__img{
    display: block;
    @include imgCover(57.5%);
  }
}

.actual-news{
  padding: 3.75em 0;
  &__grid{
    margin-top: 5.625em;
  }
}

.promo-card{
  color: #FFFFFF;
  position: relative;
  background-size: cover;
  background-position: center;
  min-height: 16.25em;
  &:before{
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: rgba(13, 13, 13, 0.4);
  }
  &__content{
    position: relative;
    z-index: 2;
    padding: 2.3125em 2.5em;
    height: 100%;
    display: flex;
    flex-direction: column;
  }
  &__title{
    font-size: 1.5em;
    line-height: 121.7%;
    letter-spacing: 0.05em;
  }
  &__desc{
    margin: 1.375em 0 2.8125em;
  }
  &__desc2{
    font-weight: 500;
    font-size: .875em;
    line-height: 105.2%;
    letter-spacing: 0.05em;
    text-transform: uppercase;
    color: rgba(255, 255, 255, 0.5);
    margin-top: auto;
  }
}

.gallery{
  padding: 3.75em 0;
  &__grid{
    margin-top: 4.5em;
    display: grid;
    grid-template-columns: repeat(2,calc(50% - 1.5625em));
    grid-template-rows: repeat(3,24.5625em);
    gap: 1.875em 3.125em;
  }
}

.gallery-card{
  &__img{
    display: block;
    @include imgCover(71.4%);
  }
  &._big{
    grid-row: span 2;
    .gallery-card__img{
      min-height: 100%;
    }
  }
  &._big2{
    grid-column: 2/3;
  }
}

.address{
  padding: 6.875em 0;
}

.map{
  margin-top: 5em;
  height: 37.5em;
}


@media screen and (max-width: 992px){
  .about-page{
    padding: 3.375em 0;
    &__top{
      display: block;
      position: relative;
      padding-top: 1.75em;
    }
    &__subtitle{
      position: absolute;
      top: 0;
      left: 0;
      margin: 0;
    }
    &__info{
      gap: 2.5em;
    }
    &__socials{
      &-item{
        margin: 0 1.25em 0 0;
        width: 1.75em;
        img{
          width: 100%;
        }
      }
      ._small{
        img{
          width: 1.375em;
        }
      }
    }
    &__bottom{
      grid-template-columns: 100%;
    }
    &__text{
      gap: 2.1875em;
    }
  }

  .interior{
    display: none;
  }

  .actual-news{
    &__grid{
      margin-top: 4em;
    }
  }

  .gallery{
    .display1{
      text-align: center;
    }
    &__grid{
      margin-top: 4em;
      grid-template-columns: 100%;
      grid-template-rows: unset;
      gap: 1.25em;
    }
  }

  .gallery-card{
    &._big{
      grid-column: unset;
      grid-row: unset;
      .gallery-card__img{
        padding-bottom: 150%;
      }
    }
  }

  .address{
    padding: 4em 0;
    .display1{
      text-align: center;
      max-width: 6em;
      margin: 0 auto;
    }
  }

  .map{
    margin-top: 4.375em;
    height: 11.875em;
  }
}





@media screen and (max-width: 576px){
  .about-page{
    &__action{
      flex-direction: column;
      margin-top: 4.375em;
      &-btn{
        margin: 1.25em auto 0 0;
      }
      ._mob-first{
        order: -1;
      }
    }
  }

  .about-contacts{
    &__group{
      @include grid100gap(.75em);
    }
    &__address{
      font-size: 1em;
    }
  }
}
