@import "../mixins";
.university{
  .banner__info{
    max-width: 54.875em;
  }
  .display1{
    line-height: 124%;
  }
}

.advantages{
  padding: 6.875em 0 6.25em;
  &__items{
    width: 50.25em;
    max-width: 100%;
    margin: 0 auto;
    display: grid;
    grid-template-columns: 1fr 1fr;
    gap: 6.25em 2em;
  }
  &__img{
    width: 100%;
    margin-top: 5em;
    display: block;
  }
}

.advantage-card{
  display: flex;
  &__left{
    width: 3.125em;
    flex-shrink: 0;
  }
  &__num{
    &:before{
      content: '/';
      color: rgba(92, 95, 94, 0.4);
    }
  }
  &__right{
    @include grid100gap(1.25em);
  }
  &__title{
    font-size: 1.375em;
    line-height: 123%;
    text-transform: uppercase;
  }
  &__desc{
    font-size: 1.125em;
    line-height: 122%;
    max-width: 14.44em;
  }
}

.university-info{
  padding: 10.3125em 0;
  background-color: $grayBg;
  &__grid{
    display: grid;
    grid-template-columns: 35em auto;
    align-items: center;
    gap: 5.625em 8.75em;
  }
  &__img{
    img{
      width: 100%;
      display: block;
    }
  }
  &__desc{
    margin-top: 2.5em;
    @include grid100gap(2em);
    p{
      font-size: 1.25em;
      line-height: 125%;
    }
  }
  &__nav{
    margin-top: 5.625em;
    display: flex;
  }
}

.history{
  padding: 7.75em 0 6em;
  &__title{
    text-align: center;
  }
  &__tabs,
  &__content{
    margin-top: 3.3125em;
  }
  ._tab{
    span{
      font-size: .75em;
      font-weight: 500;
      text-transform: uppercase;
    }
    &:not(:last-child){
      margin-right: 2.5em;
    }
  }
}

.ceo-card{
  &__title{
    text-align: center;
    font-size: 2.125em;
    line-height: 139.5%;
  }
  &__img{
    margin: 2.5em auto 0;
    width: 28.75em;
    max-width: 100%;
    display: block;
  }
  &__quote{
    font-size: 2em;
    line-height: 139.5%;
    text-align: center;
    padding: .75em 0;
    margin: 1.65em auto 0;
    max-width: 17.875em;
    position: relative;
    &:before,
    &:after{
      content: '';
      position: absolute;
      height: 1px;
      width: 5.5em;
      background-color: rgba($black,.5);
      left: 50%;
      transform: translateX(-50%);
    }
    &:before{
      top: 0;
    }
    &:after{
      bottom: 0;
    }
  }
  &__desc{
    margin: 3.25em auto 0;
    max-width: 29.5em;
    @include grid100gap(1em);
    text-align: center;
    p{
      font-size: 1.25em;
      line-height: 139.5%;
      &:nth-child(1)~p{
        display: none;
      }
    }
  }
  &__nav{
    display: flex;
    justify-content: center;
    margin-top: 3.25em;
  }
  &__btn{
    color: rgba(0, 0, 0, 0.6);
    display: flex;
    &:hover{
      color: $black;
    }
    span{
      line-height: 139.5%;
      font-size: .875em;
      transition: all .3s;
    }
    ._view2{
      display: none;
    }
    ._arrow{
      margin-left: .5em;
    }
  }
  &._active{
    .ceo-card__desc{
      p{
        &:nth-child(1)~p{
          display: block;
        }
      }
    }
    .ceo-card__btn{
      ._view1{
        display: none;
      }
      ._view2{
        display: unset;
      }
      ._arrow{
        transform: rotate(-180deg) translateY(-.2em);
      }
    }
  }
}

.vacancies{
  padding: 5em 0 7.125em;
  &__title{
    text-align: center;
  }
  &__tabs{
    margin-top: 4em;
    ._tab{
      font-weight: 700;
    }
  }
  &__content{
    margin-top: 5.375em;
  }
  &__grid{
    row-gap: 1.25em;
  }
  &__nav{
    margin-top: 4.375em;
    display: flex;
    justify-content: center;
  }
}

.vacancy-card{
  background-color: $grayBg;
  padding: 2.25em 3.75em 3.125em;
  display: flex;
  flex-direction: column;
  &__title{
    font-size: 1.25em;
    line-height: 125%;
  }
  &__props{
    padding-top: 1.875em;
    margin-top: auto;
    @include grid100gap(1.25em);
  }
  &__prop{
    display: grid;
    grid-template-columns: 1fr 1fr;
    align-items: center;
    gap: .5em;
    &-title{
      font-weight: 300;
      font-size: .75em;
      line-height: 125%;
      text-transform: uppercase;
    }
    &-desc{
      font-size: 1em;
      line-height: 125%;
    }
  }
}


@media screen and (max-width: 992px){
  .university{
    .banner__subtitle{
      display: none;
    }
  }

  .advantages{
    padding: 4em 0;
    &__items{
      gap: 3em 1em;
    }
  }

  .advantage-card{
    &__title{
      font-size: 1.25em;
    }
    &__desc{
      font-size: 1em;
      max-width: 100%;
    }
    &__left{
      width: 3.75em;
    }
    &__num{
      font-size: 1.25em;
    }
  }

  .university-info{
    padding: 3.75em 0;
    .display1{
      max-width: 90%;
    }
    &__grid{
      grid-template-columns: 100%;
    }
    &__img{
      order: 2;
    }
    &__desc{
      gap: 1.25em;
    }
    &__nav{
      margin-top: 2.5em;
    }
  }

  .history{
    padding: 3.75em 0;
    &__tabs{
      margin-top: 1.875em;
    }
    &__content{
      margin-top: 3.125em;
    }
  }

  .ceo-card{
    &__title{
      font-size: 1.5em;
    }
    &__img{
      margin-top: 3.125em;
    }
    &__quote{
      margin-top: 2.27em;
      font-size: 1.25em;
      padding: 1.1em 0;
      max-width: 12.5em;
      &:before,
      &:after{
        width: 1.9em;
      }
    }
    &__desc{
      margin-top: 3.125em;
      p{
        font-size: 1em;
      }
    }
    &__nav{
      margin-top: 1.875em;
    }
  }

  .vacancies{
    padding-bottom: 6.875em;
    &__tabs,
    &__content{
      margin-top: 3.125em;
    }
    &__nav{
      margin-top: 3.75em;
    }
  }

  .vacancy-card{
    padding: 3em 3.75em;
  }
}


@media screen and (max-width: 576px){
  .advantages{
    &__items{
      grid-template-columns: 100%;
      gap: 3.875em;
    }
  }
}
