@import "../vars";
.m-btn {
    display: flex;
    align-items: center;
    justify-content: center;
    transition: all ease-out .225s;
    padding: 0;
    &:focus {
        box-shadow: none;
    }
}

.line-btn{
    padding: 1.25em 0;
    font-weight: 500;
    text-transform: uppercase;
    position: relative;
    &:before{
        content: '';
        position: absolute;
        bottom: 0;
        left: 50%;
        width: 100%;
        height: .25em;
        transform: translateX(-50%);
        transition: inherit;
    }
    span{
        font-size: 1em;
        letter-spacing: 0.09em;
    }
    &:hover{
        &:before{
            width: 120%;
        }
    }
}


.line-btn-white{
    color: #fff;
    &:before{
        background-color: #fff;
    }
    &:hover{
        color: #fff;
    }
}

.line-btn-black{
    color: $black;
    &:before{
        background-color: $black;
    }
    &:hover{
        color: $black;
    }
}

.line-btn-red{
    color: $black;
    &:before{
        background-color: $primary;
    }
    &:hover{
        color: $black;
    }
}
