.display1{
  font-size: 4em;
  line-height: 105.2%;
  font-weight: 400;
}

.text16{
  font-size: 1em;
}

.text20{
  font-size: 1.25em;
  line-height: 125%;
}

.content-text p,
.text24{
  font-size: 1.5em;
  line-height: 125%;
  font-weight: 300;
}

.content-text p{
  &:not(:last-child){
    margin-bottom: 1em;
  }
}

.text-center{
  text-align: center;
}

.medium-weight-text{
  font-weight: 500;
}

.uppercase-text{
  text-transform: uppercase;
}


@media screen and (max-width: 992px){
  .display1{
    font-size: 2.5em;
  }

  .display1-mob{
    font-size: 2.25em;
  }

  .content-text p,
  .text24{
    font-size: 1em;
  }

  .text20{
    font-size: 1em;
  }

  .text16{
    font-size: .875em;
  }
}
