@import "../mixins";
.projects,
.main{
  overflow: hidden;
  position: relative;
  .swiper{
    &-button{
      width: .875em;
      height: auto;
      svg,
      img{
        width: 100%;
        height: auto;
      }
      &-prev{
        left: 5em;
      }
      &-next{
        right: 5em;
      }
    }
    &-pagination{
      bottom: 5.375em;
      display: flex;
      align-items: center;
      justify-content: center;
      &-bullet{
        width: .625em;
        height: .625em;
        background: rgba(255, 255, 255, 0.5);
        opacity: 1;
        margin: 0 .625em !important;
        &-active{
          background: #fff;
        }
      }
    }
  }
}

.banner{
  color: #fff;
  background-size: cover;
  background-position: center;
  position: relative;
  &:before{
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: rgba($black, 0.4);
  }
  &__inner{
    min-height: 61em;
    padding: 6.875em 0;
    display: flex;
    align-items: flex-end;
    position: relative;
    z-index: 2;
  }
  &__info{
    max-width: 50em;
  }
  &__subtitle{
    margin-bottom: 3.375em;
  }
  &__nav{
    margin-top: 1.25em;
    display: flex;
  }
}

.it-is{
  padding: 7.5em 0;
  background: $grayBg;
  &__title{
    text-align: center;
  }
  &__grid{
    width: calc(100% + 2em);
    margin: 6.625em 0 0 -1em;
    display: flex;
    justify-content: space-between;
  }
}

.it-is-card{
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
  padding: 0 .5em;
  &__desc{
    margin-top: 1.25em;
    padding-top: 1.25em;
    position: relative;
    width: 4.25em;
    max-width: 100%;
    &:before{
      content: '';
      position: absolute;
      transform: translateX(-50%);
      top: 0;
      left: 50%;
      max-width: 100%;
      width: 4.25em;
      height: 1px;
      background: $black;
    }
  }
}

.about{
  padding: 7.75em 0 8.125em;
  &__title{
    text-align: center;
    margin-bottom: .9em;
  }
  img{
    display: block;
    width: 100%;
    margin: 5em 0 6.25em;
  }
  &__grid{
    margin: 6.5em 0;
    display: flex;
    justify-content: space-between;
    align-items: center;
    p,img{
      margin: 0 !important;
    }
    &-text{
      width: 31.25em;
    }
    img{
      width: 34.75em;
    }
  }
  &__nav{
    display: flex;
    justify-content: center;
  }
}

.values{
  padding: 7.5em 0 9.25em;
  border-top: 1px solid rgba(0, 0, 0, 0.5);
  &__inner{
    max-width: 47.5em;
    margin: 0 auto;
    width: 100%;
    text-align: center;
  }
  &__desc{
    margin-top: 3.4375em;
  }
  &__img{
    margin-top: 3.4375em;
    display: block;
    width: 100%;
  }
}

.projects{
  .banner{
    &__inner{
      color: #FFFFFF;
      padding: 16.875em 0;
      align-items: center;
    }
  }
  &__subtitle{
    font-weight: 500;
    font-size: 1em;
    line-height: 132.2%;
    letter-spacing: 0.15em;
    text-transform: uppercase;
    margin-bottom: 2.1875em;
  }
  &__links{
    display: flex;
    align-items: center;
    flex-wrap: wrap;
    margin-top: 1.6875em;
  }
  &__link{
    font-size: 1em;
    line-height: 125%;
    color: rgba(255, 255, 255, 0.5);
    margin-right: 4em;
    margin-top: .5em;
    &:last-child{
      margin-right: 0;
    }
    &:hover{
      color: #fff;
    }
  }
  &__desc{
    margin-top: 2.75em;
  }
}

.delivery{
  padding: 9.375em 0 5em;
  background-color: $grayBg;
  &__inner{
    display: grid;
    grid-template-columns: 31.25em calc(100% - 37.5em);
    justify-content: space-between;
    gap: 5em;
  }
  &__img{
    img{
      width: 100%;
    }
  }
  &__title{
    text-transform: uppercase;
  }
  &__desc{
    margin-top: 3.625em;
    max-width: 28.75em;
  }
  &__nav{
    margin-top: 2.625em;
    display: flex;
  }
}

.contacts{
  padding: 6.75em 0 5em;
  &__title{
    text-align: center;
  }
  &__inner{
    display: flex;
    justify-content: space-between;
    margin-top: 7.5em;
  }
  &__info{
    max-width: 45%;
  }
  &__group{
    margin-bottom: 4.375em;
    display: grid;
    grid-template-columns: 10em auto;
    &-title{
      font-size: 1em;
      letter-spacing: 0.09em;
      text-transform: uppercase;
      font-weight: 500;
    }
    &-links{
      @include grid100gap(1em);
    }
  }
  &__nav{
    display: flex;
    margin-top: 4.75em;
  }
  &__form{
    width: 35em;
    .fg{
      margin-bottom: 1.875em;
    }
    .contacts__btn{
      margin-top: 2.5em;
    }
  }
}

.work-with{
  padding: 8.375em 0;
  background-color: $grayBg;
  &__title{
    text-transform: uppercase;
  }
  &__grid{
    display: grid;
    grid-template-columns: repeat(2,calc(50% - 1.1875em));
    gap: 2.375em;
    margin-top: 5em;
  }
  &__nav{
    margin-top: 5.625em;
    display: flex;
    justify-content: center;
  }
}

.work-with-card{
  display: flex;
  flex-direction: column;
  &__desc{
    padding-bottom: 6.25em;
  }
  &__img{
    margin-top: auto;
    @include imgCover(139%);
  }
}

.blog{
  padding: 5.8125em 0 8.75em;
  &__title{
    text-transform: uppercase;
  }
  &__grid{
    margin-top: 4.375em;
  }
}

.blog-card{
  display: flex;
  flex-direction: column;
  &__img{
    display: block;
    margin-bottom: 2.375em;
    @include imgCover(139%);
  }
  &__info{
    @include grid100gap(1em);
    margin: 1.5em 0 2.125em;
  }
  &__subtitle,
  &__title{
    text-transform: uppercase;
  }
  &__date{
    margin-top: auto;
    color: rgba($black, 0.5);
    letter-spacing: 0.05em;
    line-height: 105.2%;
  }
}


@media screen and (max-width: 992px){
  .main{
    .swiper {
      &-button {
        display: none;
      }
    }
  }

  .banner{
    &__inner{
      padding: 8.125em 0;
      min-height: 100vh;
    }
    &__info{
      max-width: 17.5em;
    }
    &__nav{
      margin-top: 1.5em;
    }
  }

  .it-is{
    padding: 5em 0;
    &__title{
      font-size: 2.25em;
    }
    &__grid{
      width: 100%;
      margin: 5em 0 0;
      display: grid;
      grid-template-columns: repeat(3,1fr);
      gap: 3em 2em;
    }
  }

  .about{
    padding: 3.75em 0;
    &__title{
      margin-bottom: 1.1em;
    }
    img{
      margin: 2.5em 0;
    }
    &__grid{
      margin: 2.5em 0;
      flex-direction: column;
      img{
        width: 100%;
        order: 2;
        margin-top: 2.5em !important;
      }
      &-text{
        width: 100%;
      }
    }
  }

  .values{
    padding: 2.8125em 0 4.125em;
    &__title{
      max-width: 7.5em;
      margin: 0 auto;
    }
    &__desc,
    &__img{
      margin-top: 2.5em;
    }
  }

  .projects{
    .banner{
      &__inner{
        padding: 15em 2.25em 16.25em;
      }
      &__info{
        max-width: 18.75em;
      }
      &__nav{
        margin-top: 3.125em;
      }
    }
    &__subtitle{
      font-size: .875em;
      margin-bottom: .78em;
    }
    &__links{
      margin-top: 0;
    }
    &__link{
      margin-top: .875em;
      margin-right: 1.25em;
    }
    .swiper{
      &-button{
        width: .625em;
        &-prev{
          left: 1em;
        }
        &-next{
          right: 1em;
        }
      }
    }
  }

  .delivery{
    padding-top: 4.375em;
    &__inner{
      grid-template-columns: 100%;
    }
    &__img{
      order: 2;
    }
    &__desc{
      margin-top: 1.875em;
      max-width: 16.25em;
    }
  }

  .contacts{
    padding: 5.3125em 0;
    &__inner{
      @include grid100gap(6.25em);
    }
    &__form,
    &__info{
      width: 100%;
      max-width: 100%;
    }
    &__groups{
      display: grid;
      grid-template-columns: 1fr 1fr;
      gap: 1em;
    }
    &__group{
      display: block;
      margin: 0;
      &-links{
        margin-top: 1.25em;
      }
    }
    &__nav{
      margin-top: 2.375em;
    }
  }

  .work-with{
    padding: 4em 0;
    &__grid{
      margin-top: 3.125em;
    }
    &__nav{
      margin-top: 2.125em;
    }
  }

  .work-with-card{
    &__desc{
      padding-bottom: 3.125em;
    }
  }

  .blog{
    padding: 4em 0 2.8125em;
    &__grid{
      margin-top: 3.75em;
    }
  }

  .blog-card{
    &__img{
      margin-bottom: 2.5625em;
    }
    &__info{
      margin: 1.125em 0;
      gap: 1.125em;
      max-width: 17.5em;
    }
    &__title{
      font-size: 1.25em;
    }
  }
}


@media screen and (max-width: 576px){
  .it-is{
    &__grid{
      grid-template-columns: 100%;
      gap: 5.375em;
    }
  }

  .work-with{
    &__grid{
      grid-template-columns: 100%;
      gap: 3.125em;
    }
  }
}
