@import "../mixins";
.footer{
  background: #0F0C0D;
  padding: 4.5625em 0;
  color: #FFFFFF;
  &__top{
    display: flex;
    justify-content: space-between;
  }
  &__logo{
    display: block;
    width: 2.875em;
    flex-shrink: 0;
    img{
      width: 100%;
      display: block;
    }
  }
  &__nav{
    display: flex;
    align-items: center;
    align-self: flex-start;
    padding: 1.25em 0;
    &-link{
      font-size: .875em;
      margin-left: 3.93em;
    }
  }
  &__bottom{
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin-top: 3.5em;
    padding-top: 2.5em;
    border-top: 1px solid rgba(255, 255, 255, 0.2);
  }
  &__c,
  &__link{
    font-size: .75em;
    line-height: 139.5%;
  }
  &__link{
    margin-left: 2.25em;
  }
}


@media screen and (max-width: 992px){
  .footer{
    padding: 3.75em 0;
    &__top{
      flex-direction: row-reverse;
      padding-right: 3.5em;
    }
    &__nav{
      flex-direction: column;
      &-link{
        margin: 0 auto 1em 0;
      }
    }
    &__bottom{
      margin-top: 4em;
      @include grid100gap(2.5em);
    }
    &__links{
      order: -1;
      display: flex;
    }
    &__link{
      max-width: 12.25em;
      margin: 0 1em 0 0;
    }
    &__c{
      color: rgba(255, 255, 255, 0.5);
    }
  }
}
