.fg {
  position: relative;
  margin: 0;
  input,textarea {
    height: 3.5em;
    border: none;
    border-bottom: 1px solid #000000;
    padding: 0;
    width: 100%;
    font-size: 1em;
    background-color: transparent;
    color: $black;
    font-weight: 500;
    letter-spacing: 0.09em;
    text-transform: uppercase;
    &::placeholder {
      color: rgba($black, 0.5);
    }
    &.input-active,
    &:focus {
      &+label{
        font-size: .75em;
        line-height: 167%;
        top: -1em;
      }
    }
  }
  textarea{
    resize: none;
    height: 8.75em;
  }
  label{
    position: absolute;
    left: 0;
    top: 1.75em;
    transition: all .3s;
    font-size: 1em;
    width: 100%;
    color: rgba($black,.5);
    pointer-events: none;
    text-transform: uppercase;
    span{
      color: $black;
    }
  }
}

.check {
  position: relative;
  input {
    position: absolute;
    opacity: 0;
    width: 100%;
    height: 100%;
    left: 0;
    top: 0;
    z-index: 2;
    cursor: pointer;
    &:checked+label {
      &::before {
        // background-color: $green;
        border: 3px solid #fff;
      }
    }
    &:disabled+label {
      color: #AAAAAA;
      pointer-events: none;
    }
  }
  label {
    padding-left: 40px;
    position: relative;
    min-height: 20px;
    font-size: 14px;
    line-height: 20px;
    cursor: pointer;
    &:before {
      content: '';
      position: absolute;
      z-index: 1;
      border-radius: 2px;
      background: rgba(255, 255, 255, 0.01);
      border: 1px solid rgba(255, 255, 255, 0.45);
      width: 20px;
      height: 20px;
      left: 0;
      top: 50%;
      transform: translateY(-50%);
      transition: all .100s;
    }
  }
}
