@import "../mixins";
.header{
  padding: 2.25em 0;
  &__inner{
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
  &__logo{
    width: 2.875em;
    display: block;
    img,
    svg{
      width: 100%;
      height: auto;
    }
  }
  &._light{
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    z-index: 5;
    color: #fff;
    .header{
      &__logo{
        path:not(.not-fill){
          fill: #fff;
        }
      }
    }
    .nav{
      &__item-show,
      &__link{
        &:hover{
          color: rgba(#fff, 0.5);
        }
      }
      @media screen and (min-width: 993px){
        &__item-hidden-inner{
          background: #fff;
        }
        &__sub-link{
          color: $black;
          &:hover{
            color: rgba($black, 0.5);
          }
        }
        &__item--active{
          .nav__item-show{
            color: rgba(#fff, 0.5);
          }
        }
      }
    }
  }
}

.nav{
  display: flex;
  align-items: center;
  &__item{
    position: relative;
    &:not(:last-child){
      margin-right: 4em;
    }
  }
  &__sub-link,
  &__item-show,
  &__link{
    font-size: 1em;
    line-height: 131.25%;
    transition: all .3s;
    &:hover{
      cursor: pointer;
      color: rgba($black, 0.5);
    }
  }
  &__item-hidden{
    min-width: 100%;
    &-inner{
      background: $grayBg;
      padding: .75em 1em;
      display: flex;
      align-items: center;
      flex-direction: column;
    }
  }
  &__sub-link{
    white-space: nowrap;
    &:not(:last-child){
      margin-bottom: .375em;
    }
  }
  @media screen and (min-width: 993px){
    &__item-hidden{
      position: absolute;
      top: 100%;
      transform: translate(-50%,-.2em);
      left: 50%;
      padding-top: 1.2em;
      opacity: 0;
      pointer-events: none;
      transition: all .3s;
    }
    &__item--active{
      .nav__item-hidden{
        opacity: 1;
        pointer-events: unset;
      }
      .nav__item-show{
        color: rgba($black, 0.5);
      }
    }
  }
}

.burger{
  display: none;
}


@media screen and (max-width: 992px){
  .header{
    &__logo{
      position: relative;
      z-index: 11;
    }
    &._light{
      .burger{
        background-image: url(../img/burger3.svg);
      }
      .nav{
        background: $black;
      }
      &._open{
        .burger{
          background-image: url(../img/burger4.svg);
        }
      }
    }
    &._open{
      .burger{
        background-image: url(../img/burger2.svg);
      }
      .nav{
        opacity: 1;
        pointer-events: unset;
      }
    }
  }

  .burger{
    position: relative;
    z-index: 11;
    background-image: url(../img/burger.svg);
    @include bgImgSize(100%);
    width: 2.1875em;
    height: 2.1875em;
    display: flex;
    transition: all .4s;
  }

  .nav{
    pointer-events: none;
    transition: all .4s;
    opacity: 0;
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100vh;
    z-index: 10;
    background-color: #fff;
    flex-direction: column;
    padding: 8.125em 1em 3em;
    &__item{
      margin: 0 auto 2em 0 !important;
    }
    &__item-show,
    &__link{
      font-size: 1.5em;
    }
    &__item{
      &-hidden{
        display: none;
        &-inner{
          background-color: transparent;
          padding: 1em 0 0 2em;
        }
      }
    }
    &__sub-link{
      font-size: 1.25em;
      margin: 0 auto 1em 0 !important;
    }
  }
}
