@import "../mixins";
.rest{
  &__grid{
    display: grid;
    grid-template-columns: 41.25em 22.6875em;
    justify-content: space-between;
    gap: 3.75em;
  }
  .about-page__info{
    max-width: 41.25em;
  }
  .about-page__text{
    margin-top: 4em;
  }
  &__right{
    margin-top: -3.125em;
  }
}

.rest-info{
  @include grid100gap(1.25em);
  &:not(:last-child){
    margin-bottom: 2.875em;
  }
  &__title{
    font-weight: 500;
    font-size: 1em;
    line-height: 197%;
    text-transform: uppercase;
  }
}

.offer{
  padding: 2.625em 0;
  &__grid{
    display: grid;
    grid-template-columns: repeat(4,calc(25% - 2.625em * 3 / 4));
    gap: 2.625em;
    margin-top: 3.375em;
  }
}

.offer-card{
  display: block;
  &__img{
    @include imgCover(100%);
  }
}

.interior2{
  padding: 2.625em 0 6.5em;
  .interior-card{
    &__img{
      padding-bottom: 125%;
    }
  }
}

.menu{
  background: #0F0C0D;
  padding: 6.875em 0;
  color: #fff;
  &__grid{
    margin-top: 6.25em;
    display: grid;
    grid-template-columns: repeat(2,calc(50% - 1.25em));
    gap: 8.125em 2.5em;
  }
}

.menu-card{
  @media screen and (min-width: 577px){
    &.translate-y{
      transform: translateY(3em);
    }
    &._big{
      .menu-card__img{
        padding-bottom: 125%;
      }
    }
    &._extra-big{
      .menu-card__img{
        padding-bottom: 150%;
      }
    }
    &._center{
      align-self: center;
    }
  }
  &__img{
    display: block;
    @include imgCover(66.6%);
    margin-bottom: 2.25em;
  }
  &__title{
    max-width: 16.25em;
    font-weight: 500;
    font-size: 1em;
    line-height: 150%;
    letter-spacing: 0.09em;
    text-transform: uppercase;
    display: block;
  }
}

.menu2,
.menu3{
  .menu-card {
    &__img {
      position: static;
      padding: 0;
      margin-bottom: 2em;

      img {
        position: static;
        width: 100%;
        height: auto;
        object-fit: unset;
      }
    }
  }
}

.menu2{
  .menu-card{
    @media screen and (min-width: 577px){
      &:nth-child(odd){
        transform: translateY(-18.75em);
      }
    }
  }
  @media screen and (min-width: 577px){
    .menu__grid{
      margin-top: 25em;
      row-gap: 5.5em;
    }
  }
}

.menu3{
  padding-bottom: 9em;
  .menu-card{
    @media screen and (min-width: 577px){
      max-width: 41.25em;
      &:nth-child(even){
        margin-left: auto;
      }
    }
  }
  @media screen and (min-width: 577px){
    .menu__grid{
      grid-template-columns: 100%;
      gap: 1.75em;
    }
  }
}

.hits,
.interior3{
  padding: 3.75em 0;
  overflow: hidden;
  &__top{
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
  &__nav{
    display: flex;
    align-items: center;
  }
  &__slider{
    margin-top: 3.25em;
  }
  .swiper{
    &-button{
      position: static;
      margin-left: 1.625em;
      width: 1.5em;
      svg{
        width: 100%;
        height: auto;
      }
    }
  }
}

.interior3{
  &__slider{
    width: calc(100% + 1.25em);
  }
  .swiper{
    &-slide{
      padding-right: 1.25em;
    }
  }
  .interior-card{
    &__img{
      padding-bottom: 64%;
    }
  }
}

.hits{
  &__slider{
    width: calc(100% + 2.375em);
  }
  .swiper{
    &-slide{
      padding-right: 2.375em;
    }
  }
}

.hit-card{
  display: block;
  &__img{
    @include imgCover(123.7%);
  }
}

.hits2{
  padding-bottom: 7.5em;
  .hit-card{
    &__img{
      padding-bottom: 113.6%;
    }
  }
}


@media screen and (max-width: 992px){
  .rest{
    &__grid{
      grid-template-columns: 100%;
      margin-top: 2.5em;
    }
    &__right{
      margin: 0;
    }
    .about-page__action{
      margin: 0;
    }
    .about-page__text{
      margin-top: 3.75em;
    }
  }

  .rest-info{
    gap: .75em;
    max-width: 16.25em;
    &:not(:last-child){
      margin-bottom: 3.75em;
    }
  }

  .offer{
    padding: 5.5em 0;
    &__grid{
      grid-template-columns: repeat(2,calc(50% - 2.125em / 2));
      gap: 2.125em;
      margin-top: 4.375em;
    }
  }

  .hits,
  .interior3{
    padding: 4em 0 2.75em;
    &__slider{
      margin-top: 2.375em;
      width: 100%;
    }
    &__nav{
      display: none;
    }
    .swiper{
      &-wrapper{
        display: grid;
        grid-template-columns: repeat(2,calc(50% - .5625em));
        gap: 1.125em;
        transform: unset !important;
      }
      &-slide{
        padding: 0;
        width: 100% !important;
      }
    }
  }

  .hits2{
    padding-bottom: 5.75em;
  }

  .offer{
    .display1{
      text-align: center;
    }
    &__grid{
      grid-template-columns: 100%;
    }
  }

  .menu{
    padding: 3.125em 0;
    &__grid{
      margin-top: 3.625em;
    }
  }

  .menu3{
    padding-bottom: 5.5em;
  }
}


@media screen and (max-width: 576px){
  .menu{
    .display1{
      text-align: center;
    }
    &__grid{
      grid-template-columns: 100%;
      gap: 2.5em;
    }
  }

  .menu-card{
    &__img{
      position: static;
      padding: 0;
      margin-bottom: 2em;
      img{
        position: static;
        width: 100%;
        height: auto;
        object-fit: unset;
      }
    }
  }

  .hits,
  .interior3{
    .swiper{
      &-wrapper{
        grid-template-columns: 100%;
      }
    }
  }

  .interior3{
    .interior-card{
      &__img{
        position: static;
        padding: 0;
        img{
          height: auto;
          position: static;
        }
      }
    }
  }

  .hit-card{
    &__img{
      position: static;
      padding: 0 !important;
      img{
        height: auto;
        position: static;
      }
    }
  }
}
