@import 'vars';
@import "mixins";
@import 'components/_text.scss';
@import 'components/_buttons.scss';
@import "components/_inputs.scss";
._grid3{
  display: grid;
  grid-template-columns: repeat(3,calc(100% / 3 - 5em / 3));
  gap: 2.5em;
}

.tabs{
  &__current{
    display: none;
  }
  &__wrap{
    @include center;
  }
}

._tab{
  color: rgba($black,.5);
  span{
    font-weight: 700;
    font-size: 14px;
    line-height: 139.5%;
  }
  &:not(:last-child){
    margin-right: 4.75em;
  }
  &:hover,
  &._active{
    cursor: pointer;
    color: $black;
  }
  &-content{
    display: none;
    &._active{
      display: block;
    }
  }
}


@media screen and (max-width: 992px){
  ._grid3{
    grid-template-columns: repeat(2,calc(50% - 1em));
    gap: 3.75em 2em;
  }
}


@media screen and (max-width: 576px){
  ._grid3{
    grid-template-columns: 100%;
  }

  .tabs{
    position: relative;
    &._active{
      .tabs__wrap{
        opacity: 1;
        pointer-events: unset;
      }
    }
    &__current{
      background-image: url(../img/arrow.svg);
      background-size: 1.5em auto;
      background-position: right 1em center;
      background-repeat: no-repeat;
      border: 1px solid $black;
      height: 3.625em;
      display: flex;
      align-items: center;
      padding: 0 2.5em 0 1.25em;
      &-title{
        font-size: .75em;
        font-weight: 500;
        text-transform: uppercase;
      }
    }
    &__wrap{
      transition: all .3s;
      opacity: 0;
      pointer-events: none;
      border: 1px solid $black;
      border-top: none;
      background-color: #fff;
      z-index: 5;
      position: absolute;
      top: 100%;
      left: 0;
      width: 100%;
      @include grid100gap(0);
    }
  }

  ._tab{
    height: 2.5em;
    display: flex;
    align-items: center;
    padding: 0 1em;
  }
}
