@import "../mixins";
.contacts-page{
  padding: 5em 0 3.75em;
  &__info{
    margin-top: 6.25em;
    @include grid100gap(2em);
    &-item{
      display: grid;
      grid-template-columns: 16.25em auto;
    }
    &-title{
      font-size: .75em;
      line-height: 125%;
      font-weight: 500;
      letter-spacing: 0.055em;
      text-transform: uppercase;
    }
    &-desc{
      font-size: 1.125em;
      line-height: 122%;
    }
  }
}

.map2{
  margin-top: 6.875em;
  height: 29.25em;
}

.consult{
  padding: 7.5em 0 10em;
  &__grid{
    display: grid;
    grid-template-columns: 1fr 1fr;
    gap: 4em;
  }
  &__content{
    max-width: 28.3125em;
  }
  &__desc{
    margin-top: 3.5em;
    p{
      font-size: .875em;
      line-height: 121%;
    }
  }
  &__form{
    &-grid{
      display: grid;
      grid-template-columns: 1fr 1fr;
      gap: 1.5em 2.75em;
    }
  }
  &__full{
    grid-column: 1/-1;
  }
  &__nav{
    display: flex;
    margin-top: 3.125em;
  }
  &__hint{
    margin-top: 1.5em;
    max-width: 24.625em;
    color: rgba(0, 0, 0, 0.4);
    p{
      font-size: .875em;
      line-height: 121%;
    }
  }
}


@media screen and (max-width: 992px) {
  .contacts-page {
    padding: 3.375em 0;
    &__info{
      margin-top: 4.25em;
      &-item{
        grid-template-columns: 11em auto;
      }
    }
  }

  .map2{
    margin-top: 4.25em;
    height: 11em;
  }

  .consult{
    padding: 3.375em 0 8.125em;
    &__grid{
      grid-template-columns: 100%;
    }
    &__content{
      max-width: 18.75em;
    }
    &__desc{
      margin-top: 2.5em;
    }
    &__form{
      &-grid{
        grid-template-columns: 100%;
        gap: 4em;
      }
    }
    &__nav{
      margin-top: 2.5em;
    }
    &__hint{
      margin-top: 1.75em;
    }
  }
}



@media screen and (max-width: 576px) {
  .contacts-page {
    &__info{
      max-width: 16.875em;
      &-item{
        grid-template-columns: 100%;
        gap: 1em;
      }
    }
  }
}
